import React, { Suspense, lazy} from 'react'
import {Redirect, Switch} from "react-router-dom";
import Loading from "../../../components/shared-components/Loading";
import VillaRoute from "../../../permissions/VillaRoute";
const Profile = ({match})=> {
    return (
        <Suspense fallback={<Loading cover={'content'}/>}>
            <Switch>
                <VillaRoute path={`${match.url}/default`} component={lazy(()=> import("./profile-default/index"))}/>
                <VillaRoute path={`${match.url}/update`} component={lazy(()=> import("./profile-default/index"))}/>
                <Redirect to={`${match.url}/default`} from={`${match.url}`}/>
            </Switch>
        </Suspense>

    )
}

export default Profile
